.storage-hero {
  padding: 50px 0 160px;

  &__text {
    margin-bottom: 64px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 40px;
  }

  &__price {
    width: 100%;
    max-width: 400px;
    border-radius: 10px;
    background-color: var(--orange);
    padding: 25px;
    color: var(--white);
  }

  &__heading {
    display: block;
    padding-bottom: 24px;
    margin-bottom: 24px;
    font-size: 18px;
    font-weight: 500;
    border-bottom: 1px solid rgba(255, 255, 255, 0.20);
  }

  &__present {
    font-size: 15px;
    font-weight: 300;
  }

  &__description-text {
    border-radius: 10px;
    background-color: var(--dark-grey);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 100% 100%;
    padding: 47px 68px;
    color: var(--white);
    line-height: 200%;
  }
}
