.information-delivery {
  padding: 48px 0px 160px;

  &__inner {

    &:not(:last-child) {
      margin-bottom: 80px;
    }
  }

  &__info {
    width: 100%;
    max-width: 904px;
    margin-bottom: 40px;
  }

  &__subtitle {
    font-size: 40px;
    margin-bottom: 32px;
  }

  &__text {

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__phone {
    color: var(--orange);
  }

  &__tabs {
  }

  &__list {
  }

  &__item {
    color: var(--1-grey);

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__tabs-text {
    color: var(--1-grey);
  }
}
