.our-business {
  padding: 40px 0 80px;

  & .container-right {
    padding-left: 0;
  }

  &__inner {
    display: flex;
    align-items: center;
  }

  &__box {
    width: 50%;
  }

  &__info {
    background-color: var(--bl);
    color: var(--white);
    padding: 85px;
  }

  &__content {
    width: 100%;
    max-width: 556px;
    margin-left: auto;
  }

  &__title {
    color: var(--white);
    margin-bottom: 32px;
  }

  &__container-text {
    margin-bottom: 40px;
  }

  &__text {
    color: var(--3-grey);

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__image {
    border-radius: 10px;
  }
}

