.for-questions {
  padding: 52px 0 40px;
  background-color: var(--orange);
  background-repeat: no-repeat;
  background-size: 1072px 1072px;
  background-position: center right;

  &__inner {
    width: 100%;
    max-width: 620px;
  }

  &__title {
    color: var(--white);
    margin-bottom: 32px;
  }

  &__form {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
  }

  &__label {
    width: 48.6%;
  }

  &__label--person {
  }

  &__input {
    width: 100%;
    border-radius: 10px;
    background-color: var(--white);
    padding: 14px 16px;

    &::-webkit-input-placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
    &:-moz-placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
    &::-o-placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
    &:-ms-input-placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
    &::placeholder {
      color: var(--light-grey);
      font-weight: 300;
    }
  }

  &__input--textarea {
    width: 100%;
    min-height: 154px;
    max-height: 250px;
    resize: vertical;
    border: none;
  }

  &__label--mail {
  }

  &__label--phone {
  }

  &__label--company {
  }

  &__label--textarea {
    width: 100%;
  }

  &__btn {
    width: 100%;
    text-align: center;
    color: var(--orange);
    background-color: var(--white);
    border-radius: 10px;
    padding: 17px;
  }
}
