.documents-guarantee {
  padding: 80px 0;

  &__subtitle {
    font-size: 24px;
    margin-bottom: 48px;
    font-family: var(--font-Inter);
  }

  &__list {
    margin-bottom: 32px;
  }

  &__item {
    padding-left: 31px;
    background-repeat: no-repeat;
    background-size: 15px 19px;
    background-position: left center;

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__text {
    padding: 24px;
    border-radius: 10px;
    background: var(--bg);
    line-height: 200%;
  }
}
