.our-team {
  padding: 80px 0;

  &__title {
    margin-bottom: 80px;
  }

  &__inner {
    display: flex;
    gap: 66px;
  }

  &__cell {
  }

  &__cell--info {
    width: 53%;
  }

  &__text {
    margin-bottom: 64px;
  }

  &__heading {
    font-size: 20px;
    margin-bottom: 26px;
    font-family: var(--font-Inter);
  }

  &__list {
  }

  &__item {
    padding-left: 31px;
    background-repeat: no-repeat;
    background-position: 0 6px;
    background-size: 15px 19px;

    &:not(:last-child) {
      margin-bottom: 19px;
    }
  }

  &__cell--img {
    width: 47%;
  }

  &__image {
  }
}
