.placing-order {
  padding: 40px 0 160px;

  &__title {
    font-size: 40px;
    margin-bottom: 40px;
  }
}

.registration-stage {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;

  &__item {
    border-radius: 10px;
    border: 1px solid var(--3-grey);
    padding: 40px;
    width: 100%;
  }

  &__item--payment {
    max-width: 896px;
  }

  &__item--delivery {
    max-width: 368px;
  }

  &__item--your-order {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 88px;
  }

  &__subtitle {
    font-weight: 400;
    line-height: 123%;
    margin-bottom: 32px;
  }

  &__subtitle--you-order {
    margin-bottom: 0;
    margin-right: auto;
  }

  &__fieldset-form {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
  }

  &__fieldset {
    display: flex;
    flex-direction: column;
    gap: 14px;
  }

  & .custom-checkbox {
    margin-bottom: 0;
  }

  &__label--person {
    width: 49%;
  }

  &__label--city {
    width: 27%;
  }

  &__label--street {
    width: 49%;
  }

  &__label--house {
    width: 9%;
  }

  &__label--apartment {
    width: 9%;
  }

  &__label--phone {
    width: 49%;
  }

  &__label--mail {
    width: 49%;
  }

  &__label--textarea {
    width: 100%;
    min-height: 160px;
  }

  &__input {
    width: 100%;
    padding: 16px;
    border-radius: 10px;
    background-color: var(--bg);
    color: var(--light-grey);

    &::-webkit-input-placeholder {
      color: var(--light-grey);
    }
    &:-moz-placeholder {
      color: var(--light-grey);
    }
    &::-o-placeholder {
      color: var(--light-grey);
    }
    &:-ms-input-placeholder {
      color: var(--light-grey);
    }
    &::placeholder {
      color: var(--light-grey);
    }
  }

  &__textarea {
    border: none;
    width: 100%;
    height: 100%;
    max-height: 400px;
    color: var(--light-grey);
    background-color: var(--bg);
    padding: 16px;
    border-radius: 10px;
    resize: vertical;

    &::-webkit-input-placeholder {
      color: var(--light-grey);
    }
    &:-moz-placeholder {
      color: var(--light-grey);
    }
    &::-o-placeholder {
      color: var(--light-grey);
    }
    &:-ms-input-placeholder {
      color: var(--light-grey);
    }
    &::placeholder {
      color: var(--light-grey);
    }
  }

  &__legend {
    color: var(--bl);
    font-family: var(--font-Inter);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.18px;
    margin-bottom: 17px;
  }

  &__fieldset--delivery {
    margin-bottom: 24px;
  }

  &__row-total {
    width: 100%;
    max-width: 346px;
  }

  &__total-inner {
    display: flex;
    flex-wrap: wrap;
    gap: 38px;
    color: var(--bl);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
    border-bottom: 1px solid var(--3-grey);
    padding-bottom: 24px;
    margin-bottom: 40px;
  }

  &__btn {
    width: 100%;
    margin-bottom: 10px;
  }

  &__policy {
    color: var(--2-grey);
    font-size: 12px;
    line-height: 160%;
  }

  &__policy-link {
    font-size: 12px;
    color: var(--orange);
  }
}

.custom-checkbox {

  &__delivery {
    position: relative;
    padding-left: 34px;
    color: var(--2-grey);
    cursor: pointer;

    &:before,
    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border-radius: 50%;
    }

    &:before {
      width: 24px;
      height: 24px;
      border: 1px solid var(--light-grey);
    }
  }

  & input:checked + .custom-checkbox__delivery::before {
    background-color: var(--orange);
    border: 1px solid transparent;
  }

  & input:checked + .custom-checkbox__delivery::after {
    width: 12px;
    height: 12px;
    left: 6px;
    background-color: var(--white);
  }
}

.delivery-type {
  width: 100%;
  max-width: 346px;

  &__heading {
    display: block;
    color: var(--orange);
    font-family: var(--font-Inter);
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.24px;
    margin-bottom: 24px;
  }

  &__item {
    margin-bottom: 16px;
  }

  &__head {
    display: block;
    font-weight: 400;
    margin-bottom: 3px;
  }

  &__person-info {
    font-weight: 300;
    color: var(--1-grey);
  }
}
