.no-service {
  padding: 40px 0 80px;

  &__subtitle {
    font-size: 24px;
    margin-bottom: 48px;
    font-family: var(--font-Inter);
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 40px;
  }

  &__info {
    width: 100%;
    max-width: 840px;
  }

  &__list {
    border-radius: 10px;
    background: var(--orange);
    padding: 40px;
    padding-left: 60px;
    margin-bottom: 16px;
    color: var(--white);
    list-style-type: disc;
  }

  &__item {
    line-height: 160%;

    &:not(:last-child) {
      margin-bottom: 18px;
    }
  }

  &__text {
    padding: 40px;
    border-radius: 10px;
    background-color: var(--bg);
  }

  &__image {
  }
}

.color-red {
  color: var(--red);
  font-size: 24px;
  font-weight: 500;
}
