.not-transportation {
  padding: 64px 0 80px;

  &__title {
    font-size: 110px;
    font-weight: 400;
    margin-bottom: 80px;
  }

  &__inner {
  }

  &__list {
    display: flex;
    gap: 40px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    max-width: 400px;
    background-repeat: no-repeat;
    background-position: bottom right;
    border-radius: 10px;
    background-color: var(--dark);
    padding: 32px;
    color: var(--white);
    transition-property:  background-color;
    transition-duration: 0.1s;
    transition-timing-function: ease-in;
    transition-delay: 0s;

    &:hover {
      background-color: var(--orange);
    }
  }

  &__heading {
    margin-bottom: 16px;
    font-family: var(--font-Inter);
    font-size: 24px;
    color: var(--white);
  }

  &__text {
    color: var(--3-grey);
    margin-bottom: 32px;
    flex: 1 1 auto;
  }

  &__link {
  }
}

.orange-text {
  color: var(--orange);
}
