.pagination-site {

  &__nav--no-bg {
    margin-bottom: 0;
  }

  &__link--product {
    color: var(--1-grey);
  }
}

.product {
  margin-bottom: 75px;

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    align-items: start;
    grid-template-areas:
      "product__images product__name"
      "product__images product__description"
      "product__images product__description"
    ;
    margin-bottom: 75px;
  }

  &__images {
    grid-area: product__images;
    width: 100%;
    max-width: 623px;
  }

  &__grid-item--small {
    display: inline-block;
    width: 180px;
    margin-right: 15px;
    opacity: 0.5;
    transition-property: opacity;
    transition-duration: 0.2s;
    transition-timing-function: ease-in;
    transition-delay: 0s;

    &:hover {
      opacity: 1;
    }
  }

  &__name {
    grid-area: product__name;
    width: 100%;
    max-width: 510px;
    margin-left: auto;
    margin-bottom: 40px;
  }

  &__title {
    font-size: 40px;
    margin-bottom: 20px;
  }

  &__subtitle {
    font-size: 40px;
    margin-bottom: 40px;
  }

  &__description-text {
    margin-bottom: 80px;
  }

  &__additional-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__status {
    color: var(--green);
  }

  &__description {
    grid-area: product__description;
    width: 100%;
    max-width: 510px;
    margin-left: auto;
  }

  &__price-wrapper {
    margin-bottom: 32px;
  }

  &__price-container {
    margin-bottom: 40px;
  }

  &__price {
    color: var(--bl);
    font-family: var(--font-Inter);
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.4px;
  }

  &__price--old {
    color: var(--light-grey);
    font-size: 18px;
    letter-spacing: 0.18px;
    text-decoration-line: line-through;
    margin-left: 16px;
  }

  &__text {
    color: var(--1-grey);
  }

  &__size {
    margin-bottom: 32px;
  }

  &__color {
    margin-bottom: 48px;
  }

  &__head {
    display: inline-block;
    margin-bottom: 24px;
  }

  &__basket {
    display: flex;
    align-items: center;
    gap: 16px;
  }

  &__favorites-btn {
    width: 52px;
    height: 52px;
    border-radius: 10px;
    background-color: #F8F8F8;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 32px 29px;
    cursor: pointer;
  }

  &__characteristics {
    margin-bottom: 80px;
  }
}

.vendor-code {

  &__span {
    display: block;
    margin-bottom: 12px;
    color: var(--light-grey);
  }
}

.social {
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  &__item {

    height: 21px;

    &:not(:last-child) {
      margin-right: 8px;
    }
  }

  &__link {
    background-size: 22px 21px;
    background-position: center center;
    background-repeat: no-repeat;
    width: 22px;
    height: 21px;
    transition-property: opacity;
    transition-duration: 0.2s;
    transition-timing-function: ease-in;
    transition-delay: 0s;

    &:hover {
      opacity: 0.6;
    }
  }
}

.size {

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {

    &:not(:last-child) {
      margin-right: 16px;
    }
  }
}

.custom-checkbox {

  &__size {
    display: inline-block;
    padding: 10px;
    min-width: 56px;
    border-radius: 5px;
    text-align: center;
    border: 1px solid var(--3-grey);
    cursor: pointer;
  }
}

.custom-checkbox--size {
  margin: 0;

  & input:checked + .custom-checkbox__size {
    background-color: var(--bl);
    color: var(--white);
  }
}

.custom-checkbox--color {
  margin: 0;
}

.color {

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {

    &:not(:last-child) {
      margin-right: 13px;
    }
  }
}

.items {

  &__control {
    width: 30px;
    cursor: pointer;
    transition: 0.2s ease-in;
    text-align: center;
  }

  &__current {
    min-width: 40px;
    text-align: center;
  }
}

.counter-wrapper {
  border: 1px solid #f2ede7;
  border-radius: 8px;
  min-width: 83px;
  height: 52px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  padding: 10px;
  overflow: hidden;
}
