.have-questions {
  padding: 45px 0 36px;
  background-color: var(--orange);
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: cover;
  color: var(--white);

  &__inner {
    width: 100%;
    max-width: 534px;
    margin-right: auto;
  }

  &__title {
    margin-bottom: 32px;
    color: var(--white);
  }

  &__box-phone {
    margin-bottom: 32px;
  }

  &__head {
    display: block;
    margin-bottom: 16px;
    font-size: 20px;
    font-weight: 500;
  }

  &__link {
    font-size: 64px;
    line-height: 1.23;
    font-family: var(--font-Bebas);
    transition: color 0.3s;

    &:hover {
      color: var(--3-grey);
    }
  }

  &__text {
    font-weight: 300;
  }
}
