.about-store {
  padding: 80px 0 160px;

  &__title {
    margin-bottom: 48px;
  }

  &__inner {
    display: flex;
    margin-bottom: 40px;
  }

  &__description {
  }

  &__info {
    color: var(--2-grey);
  }

  &__bg {
    width: 100%;
    min-height: 533px;
    margin-bottom: 56px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }

  &__heading {
    display: inline-block;
    margin-bottom: 48px;
    font-family: var(--font-Inter);
    font-size: 24px;
  }

  &__slide {
    padding: 40px;
    border-radius: 10px;
    color: var(--white);
    width: 100%;
    max-width: 500px;
  }

  &__slide--bicicles {
    background-color: var(--dark-grey);
  }

  &__slide--accessories {
    background-color: var(--bl);
  }

  &__slide--spare-parts {
    background-color: var(--orange);
  }

  &__slide--equipment {
    background-color: var(--1-grey);
  }

  &__caption {
    margin-bottom: 24px;
    font-family: var(--font-Inter);
    font-size: 24px;
    color: var(--white);
  }

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 15px;
  }

  &__item {
  }

  &__link {
    font-weight: 300;

    &:hover {
      text-decoration: underline;
    }
  }
}


