.basket {
  padding: 20px 0 140px;

  &__title {
    margin-bottom: 31px;
  }

  &__inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 40px;
  }

  &__items-cart {
    width: 100%;
    max-width: 949px;
  }

  &__cart-navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 10px;
    padding: 5px 0;
  }

  &__return-shopping {
    color: var(--orange);
  }

  &__empty-trash {
    color: var(--light-grey);
  }

  &__cart {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 72px;
    padding: 10px 0;
    padding-right: 127px;
    border-bottom: 1px solid var(--3-grey);
  }

  &__model {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    max-width: 452px;
    gap: 72px;
  }

  &__model-name {
    font-family: var(--font-Inter);
    font-size: 18px;
    font-weight: 300;
  }

  &__model-image {
    max-width: none;
    height: 161px;
  }

  &__model-price {
    display: flex;
    align-items: center;
    gap: 72px;
  }

  &__current-price {
    color: var(--bl);
    font-family: var(--font-Inter);
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: 0.2px;
  }

  &__price-old {
    display: block;
    color: var(--light-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%;
    text-decoration-line: line-through;
  }

  &__information-cart {
    width: 290px;
    border-radius: 10px;
    border: 1px solid var(--3-grey);
    padding: 32px 24px;
  }

  &__order-number {
  }

  &__info-head {
  }

  &__info-number {
    color: var(--bl);
    font-weight: 400;
  }

  &__order-price {
  }

  &__info-price {
    color: var(--bl);
    font-weight: 400;
    text-align: right;
  }

  &__discount {
  }

  &__total {
  }

  &__information-row {
    border-bottom: 1px solid var(--bg);
    padding-bottom: 24px;
    margin-bottom: 24px;
  }

  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    color: var(--2-grey);
    font-family: var(--font-Inter);
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 160%;

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__info-head--total,
  &__info-price--total {
    font-size: 24px;
    font-weight: 500;
    color: var(--bl);
  }

  &__btn-delete {
    position: absolute;
    top: 50%;
    right: 60px;
    transform: translateY(-50%);
  }

  &__svg-delete {
    width: 16px;
    height: 18px;
    stroke: var(--light-grey);
    transition-property: stroke;
    transition-duration: 0.1s;
    transition-timing-function: ease-in;
    transition-delay: 0s;

    &:hover {
      stroke: var(--bl);
    }
  }

  &__btn {
    width: 100%;
  }
}
